body {
  --background-dark: #272838;
  --background-light: #f9f8f8;
  --accent-dark: #f3de8a;
  --accent-light: #7e7f9a;

  --knob-size: 25px;

  background-color: var(--background-dark);
  color: var(--background-light);
  font-family: Arial, Helvetica, sans-serif;

  user-select: none;
}

.project-title {
  min-height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  text-decoration: none;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 16px auto 48px auto;
  max-width: 800px;
  text-align: center;
  gap: 16px;
}

.activity-suggestion {
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 1.2rem;
}

.avatar-container {
  align-self: center;
  justify-self: center;
}

.intro-container,
.intro-container > * {
  color: var(--accent-dark);
  white-space: pre-line;
}

.avatar-img {
  border-radius: 100%;
  width: 150px;
  border: 2px solid var(--accent-dark);
  box-shadow: var(--accent-light) 0 0 5px 5px;
}

.project-poster {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 400px;
  max-width: 500px;
  height: 150px;
  margin: 32px auto;

  background-color: var(--accent-dark);
  color: var(--background-dark);
  box-shadow: var(--accent-light) 0 0 5px 5px;
  border-radius: 2rem;
  padding: 0.4em;
}
.project-poster a {
  color: var(--background-dark);
}

.clickable {
  cursor: pointer;
}

@media (max-width: 650px) {
  .container {
    margin: 8px 8px;
  }

  .project-poster {
    width: 80%;
  }
}

.knob {
  touch-action: none;
  width: var(--knob-size);
  height: var(--knob-size);
}
.know:hover {
  cursor: pointer;
}
